
body {
    background: #fff;
    font-size: 14px;
    font-family: 'Prata', serif;
}

.logo {
    width: 150px;
}

.navbar-brand {
    opacity: 1;
}

.grey {
    color: #868282;
    &:hover {
        color: #393B3B;
    }
}

a {
    color: #393B3B;
    text-decoration: none;
    margin-right: 33px;
    &:hover {
        text-decoration: none;
        color: #121414;
    }
}

.main_btn {
    margin-bottom: 10%;
    width: 249px;
    padding: 15px 20px;
    border: none;
    border-radius: 0;
    left: 438px;
    font-size: 16px !important;
    font-family: 'HelveticaNeueCyr-Roman', sans-serif;
    line-height: normal;
}

.main_img {
    background-image: url("../img/bg.jpg");
    background-repeat: no-repeat;
    background-position: bottom;
    -webkit-background-size: 100%;
    background-size: 100%;
    height: 80vh;
    h1 {
        font-family: "HelveticaNeueCyr-Roman", sans-serif;
        font-weight: 100;
        position: absolute;
        top: 30%;
        right: 25%;
        font-size: 50px;
        background: white;
    }
}

.fbtn {
    background: #F47B37;
    margin-right: 66px;
}

.sbtn {
    /*color: #393B3B;*/
    color: #f7f7f7;
    border: 1px solid #F47B37;
}

.main_btn:focus {
    box-shadow: 0 0 0 0.2rem rgba(232, 197, 151, 0.43) !important;
}

.fbtn {
    &:hover, &:active {
        color: #fff;
        background-color: #e35633 !important;
        border: none;
    }
}

.sbtn {
    &:hover {
        color: #fff;
        background-color: #e45633;
        border: none;
    }
    &:active {
        color: #fff;
        background-color: #e45633 !important;
        border: none;
    }
}

.btn {
    font-size: 15px;
}

.btn-outline-success {
    color: #393B3B;
    border-color: #393B3B;
    &:hover {
        color: #fff;
        background-color: #393B3B;
        border-color: #393B3B;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba(197, 197, 197, 0.5) !important;
    }
    &:not(:disabled):not(.disabled):active {
        color: #fff;
        background-color: #393B3B;
        border-color: #393B3B;
    }
}

.form-control {
    font-size: 14px;
    &:focus {
        color: #495057;
        border-color: #a9b1b9;
        box-shadow: 0 0 0 0.2rem rgba(203, 206, 206, 0.54);
    }
}

.bag {
    margin-left: 15px;
}

.carousel-caption {
    position: absolute;
    right: 15%;
    bottom: 10%;
    left: 15%;
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
    text-align: center;
}

.pic {
    background: #ffffff;
    display: flex;
    flex-flow: row wrap;
    height: 100vh;
}

.block {
    display: flex;
    width: 50%;
    justify-content: center;
    align-items: center;
    -moz-transition: all 1s linear;
    -o-transition: all 1s linear;
    -webkit-transition: all 1s linear;
    &:hover {
        cursor: pointer;
        > h3 {
            display: none;
        }
    }
}

.him {
    background: #c4c4c4;
    &:hover {
        background: url("../img/him.jpg") no-repeat center;
    }
}

.her:hover {
    background: url("../img/her.jpg") no-repeat center;
}

footer {
    background: url("../img/footer.png");
}

a {
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-size: 14px;
    opacity: 0.8;
    color: #ffffff;
    &:hover, &.wht {
        color: #FFFFFF;
        opacity: 1;
    }
}

li {
    list-style-type: none;
}

footer p {
    font-family: 'HelveticaNeueCyr-Roman', sans-serif;
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-size: 14px;
    opacity: 0.8;
}

form {
    width: 248px;
    height: 221.5px;
    left: 952px;
    top: 74.5px;
    background: #ECECEC;
}

.application-invite {
    position: relative;
    margin: 40px 0;
    padding: 60px 0;
    background: #f2f5f7;
    &:before {
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        margin-left: -30px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 30px 50px 30px;
    }
}

a.curs {
    cursor: default;
}

.main-menu {
    > li {
        position: relative;
    }
    .sub-menu {
        position: absolute;
        top: 40px;
        left: -80px;
        z-index: 10;
        width: 220px;
        height: 220px;
        margin: 0;
        padding: 20px;
        list-style: none;
        background-color: #f9f9f9;
        box-shadow: 0 3px 6px 2px #273e3e24;
    }
}

.sub-menu {
    display: none;
    transition: display 3s linear 4s;
}

//.main-menu > li:hover .sub-menu {
//    display: block;
//}

.sub-menu {
    li {
        &:first-child {
            margin-bottom: 10%;
            text-align: center;
        }
        &:last-child input {
            width: 96%;
            display: block;
            border-radius: 0;
            color: #fff;
        }
    }
    &:after {
        content: '';
        position: absolute;
        top: -10px;
        left: 50%;
        margin-left: -10px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 10px 10px;
        border-color: transparent transparent #ECECEC;
    }
    form {
        width: 200px !important;
    }
    input[type="submit"] {
        width: 90% !important;
    }
}
input[type="text"], input[type="password"], input[type="email"], input[type="number"] {
    margin-bottom: 14px;
    font-family: 'HelveticaNeueCyr-Light', sans-serif;
    border: 1px solid #b5b0b0 !important;
    padding: 2px 5px !important;
}

















